import * as React from 'react';
import Typography from '@material-ui/core/Typography';
import MuiLink from '@material-ui/core/Link';
import { ContentBasis } from './Basis';
import { ClubTemplateQuery } from '../../graphql-types';

interface Props {
  clubsYaml: ClubTemplateQuery['clubsYaml'];
}

function ClubInfo({ clubsYaml }: Props) {
  return (
    <ContentBasis>
      <Typography variant="h6" component="h2" gutterBottom>
        {clubsYaml?.name}
      </Typography>
      <Typography variant="body2" component="ul">
        <li>: {clubsYaml?.fullname}</li>
        <li>: {clubsYaml?.company}</li>
        <li>: {clubsYaml?.category}</li>
        <li>: {clubsYaml?.hometown}</li>
        <li>: {clubsYaml?.area}</li>
        {clubsYaml?.settlement ? (
          <li>
            :{' '}
            <MuiLink href={clubsYaml.settlement} color="secondary" target="_blank" rel="noopener noreferrer">
              {clubsYaml.settlement}
            </MuiLink>
          </li>
        ) : null}
        {clubsYaml?.relatedCompanies ? <li>: {clubsYaml.relatedCompanies.join(', ')}</li> : null}
      </Typography>
    </ContentBasis>
  );
}

export default ClubInfo;
